.epmployment_main{
    width:100%;
    min-height: 100vh;
    background-color: rgb(250,250,250);
    display: flex;
    justify-content: space-around;
    align-items: center;
    transition: all 0.5 ease-in-out;
}
.epmployment_container{
    display: flex;
    flex-direction:column;
}
.epmployment_h1{
    font-size: 48px;
    font-weight: bold;
    color:rgb(48,48,48);
    display: flex;
    flex-direction: column;
}
.epmployment_h2{
    color:rgb(112,112,112);
    font-size: 18px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}
.epmployment_h3{
    margin-top: 40px;
    font-size: 20px;
    display: flex;

}
.epmployment_sign{
    width:160px;
    height: 45px;
    color:white;
    background-color: rgb(35,56,98);
    border:none;
    cursor: pointer;
}
.epmployment_para{
    width: fit-content;
}
.epmployment_container_box{
    display: flex;
    justify-content: space-between;
}
.employInput{
    width: 95%;
    height: 35px;
    border:solid 1px rgb(20,138,117);
    font-size: 18px;
    color:rgb(112,112,112);

}
@media screen and (max-width:710px) {
    .epmployment_h1{
        font-size: 36px;
    }
    .epmployment_h2{
        font-size: 16px;
    }
}
@media screen and (max-width:600px) {
    .epmployment_h3{
        margin-top: 30px;
        display: flex;
        align-items: center;
    }
    .epmployment_h1{
        font-size: 26px;
    }
    .epmployment_h2{
        font-size: 14px;
    }
}
@media screen and (max-width:425px) {
    .epmployment_h3{
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    .epmployment_sign{
        width: 150px;
    }
    .epmployment_h1{
        font-size: 18px;
    }
    .epmployment_h2{
        font-size: 12px;
    }
    .epmployment_main{
        height: fit-content;
        padding-top: 70px;
        padding-bottom: 70px;
    }
    .epmployment_container{
        align-items: center;
        justify-content: center;
    }
}
